@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Paytone+One&display=swap);
:root {
  --colorClair: #006f66;
  --colorFonce: #054a44;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
}

button:focus {
  outline: none;
}

.container_score {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin-left: 3%; */
}

.question_number {
  border: 12px solid var(--colorClair);
  font-size: 96px;
}

.score {
  font-size: 36px;
}

.header_container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex: 0 1;
}

.header_title {
  width: 854px;
  text-align: center;
  margin-right: 10%;
  margin-bottom: 59px;
  position: relative;
  font-size: 72px;
}
.header_title::before {
  content: "";
  z-index: -1;
  position: absolute;
  width: 853px;
  height: 12px;
  left: 0;
  bottom: -12px;
  background: linear-gradient(to right, #006f66, #2fb9ae);
}

.questionPropositionContainer {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column-reverse;
  background: var(--colorClair);
}

.questionPropositionContainer::before {
  content: "";
  position: absolute;
  z-index: 8;
  left: 0;
  top: 50%;
  width: 100%;
  height: 37px;
  background: var(--colorFonce);
}

.proposition {
  position: absolute;
  text-align: center;
  color: white;
  font-size: 96px;
  top: 41%;
  width: 60%;
  z-index: 10;
  font-family: "Paytone One";
  box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  background: var(--colorClair);
}

.button {
  font-size: 96px;
  font-family: "Paytone One";
  cursor: pointer;
  color: white;
  background: var(--colorClair);
  border: none;
  width: 100%;
  height: 50%;
  box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
}

.button:hover {
  background: var(--colorFonce);
}

.container_reponse {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
}

.reponse {
  color: white;
  font-family: "Paytone One";
  font-size: 144px;
}

.description {
  color: white;
  font-size: 36px;
  margin-top: -35px;
  margin-bottom: 36px;
}

.buttonNext {
  font-size: 48px;
  padding: 35px 114px;
  font-family: "Montserrat";
  color: white;
  background-color: var(--colorFonce);
  border: none;
  cursor: pointer;
}

.container_footer {
  background-color: var(--colorClair);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 1;
}

.question {
  font-size: 48px;
  width: 100%;
  text-align: center;
  color: white;
  background: var(--colorFonce);
}

.application {
  height: 100vh;
  display: flex;
  flex-direction: column;
}



