.header_container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex: 0 1;
}

.header_title {
  width: 854px;
  text-align: center;
  margin-right: 10%;
  margin-bottom: 59px;
  position: relative;
  font-size: 72px;
}
.header_title::before {
  content: "";
  z-index: -1;
  position: absolute;
  width: 853px;
  height: 12px;
  left: 0;
  bottom: -12px;
  background: linear-gradient(to right, #006f66, #2fb9ae);
}
